@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.container {
  width: 90%;
  max-width: 1300px;
  padding: 50px 0;
  margin: 0 auto;
}

.title_head {
  font-size: 36px;
  font-weight: 600;
  line-height: 43.88px;
  color: #4e5150;
}

.cc {
  font-size: 14px;
  margin: 0 auto;
  text-align: center;
  font-weight: 700;
  color: #BDBDBD;
}

@media (max-width: 600px) {
  .title_head {
    font-size: 24px;
  }

  .cc {
    font-size: 12px;
  }
}


label, span {
    font-weight: 600;
    font-size: 12px;
    line-height: 14.63px;
    color: #4e5150;
    font-family: 'Montserrat', sans-serif;
}

.section_title h3{
    font-weight: 600;
    font-size: 18px;
    line-height: 21.94px;
    color: #4e5150;
    font-family: 'Montserrat', sans-serif;
    margin-top: 50px;
    margin-bottom: 20px;
}

.mb {
    margin-top: 100px;
}

.check-b {
    margin-left: 10px;
}

.check-b span {
    margin: 0 auto;
    -webkit-align-self: center;
            align-self: center;
    -webkit-align-content: center;
            align-content: center;
    justify-self: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.btn-primary {
    border-radius: 12px;
    background-color: #F2994A;
    float: right;
    font-size: 12px;
    font-weight: 800;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    padding: 10px 25px;
}

@media (max-width: 600px) {
    label, span {
        font-size: 10px;
    }

    .section_title h3{
        font-size: 14px;
    }
}
.ck-box {
    -webkit-align-self: flex-start;
            align-self: flex-start;
    justify-self: flex-start;
}

.checkout-box {
    background: #F2F2F2;
    width: 90%;
    padding: 20px;
    margin: 0 auto;
    /* min-width: 350px; */
    /* height: 535px; */
    border-radius: 12px;
}

.box-grid {
    margin: 40px auto;
}
.product_name {
    font-weight: 600;
    font-size: 16px;
    line-height: 19.5px;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 0px;
}

.prices {
    font-family: 'Montserrat', sans-serif;
    margin-top: 0px;
}

.new_price {
    font-size: 16px;
    font-weight: 600;
    color: #f2994a;
}

.old_price {
    font-size: 12px;
    font-weight: 600;
    line-height: 14.63px;
    color: #4e5150;
    text-decoration: line-through;
    margin-left: 10px;
}

.text_col {
    padding-left: 0;
}

.image img {
    width: 90%;
    min-width: 120px;
    max-width: 100%;
    border-radius: 12px;
}

.price_c {
    margin-top: 130px;
}

.cost_add, .t-tal {
    border-top: 2px solid #000;
    margin: 10px auto;
}

.cost_add p, .t-tal p {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 21.94px;
    
}

.shipping_p p {
    float: right;
}
@media (max-width: 600px) {
    .checkout-box {
      -webkit-order: 1;
              order: 1;
    }
}
@media (max-width: 600px) {
    .checkout-box {
        /* min-width: 320px; */
        margin: 0 auto;
        width: 100%;
    }

    .image img {
        width: 40%;
        min-width: 100px;
        max-width: 100%;
        border-radius: 12px;
    }

    .container {
        padding-left: 0px;
        padding-right: 0px;
    }

    .cost_add p, .t-tal p {
        font-size: 14px;
        line-height: 17px;
    }

    .product_name {
        font-size: 12px;
    }

    .new_price {
        font-size: 14px;
    }
    
    .old_price {
        font-size: 10px;
    }
}
.btn1 {
    padding: 4px 7px;
    margin: 0 auto;
    border: 4px;
    background: #E0E0E0;
    font-weight: 600;
    line-height: 1.5;
    cursor: pointer;
}

/* .counter_body {
    padding: 0px;
} */

.counter {
    margin: 30px 0 0 0;
    border: 1px solid #828282;
    padding: 5px;
    max-width: 120px;
    min-width: 100px;
    border-radius: 12px;
    -webkit-align-self: baseline;
            align-self: baseline;
}

.c_info {
    margin: 0 auto;
}

span {
    margin: 0 10px;
}

span:first-of-type {
    margin-left: 0;
}

span:last-of-type {
    margin-right: 0;
}
